import React, { useEffect, useState } from 'react';
import { useHistory, useParams } from 'react-router-dom'
import styled from 'styled-components'
import './Detail.scss'

let 박스 = styled.div`
    padding : 20px;
    // background : black;
`
let 제목 = styled.h4`
   font-size : 25px;
//    color:${props => props.색상} 
`

// class Detail2 extends React.Component{
//   componentDidMount(){
//     //Detail2 컴포넌트가 Mount가 되었을 때 실행할 코드 (등장)
//     //Ajax 같은 것도 이런 곳에 자주 사용
//   }
//   componentWillUnmount(){
//     //Detail2 컴포넌트가 UnMount가 되기 직전에 실행할 코드 (빠이빠이)
//   }
// }


function Detail(props){

  let [alert, alert변경] = useState(true);
  let [inputData,inputData변경] = useState('');

    useEffect(()=>{
    // useEffect훅 , 컴포넌트 mount 되었을 때 컴포넌트가 update 될 때 특정 코드를 실행할 수 있음.
    
      // Detail 페이지 방문후 alert 창이 2초 후에 사라지게
      let 타이머 = setTimeout(()=>{ alert변경(false) }, 2000)
      console.log('안녕')
      return ()=>{ clearTimeout(타이머)}
      // useEffect훅2 , 컴포넌트 사라질때 코드를 실행시킬 수도 있음
      // return function 어쩌구(){ 실행할 코드 ~~ }

      // useEffect훅3 , 여러개를 사용하고 싶다면
      // useEffect() 를 여러개 사용
    },[alert]);



    let {id} =  useParams();
    let 찾은상품 = props.shoes.find(function(상품){
        return 상품.id == id
    });
    // let 찾은상품 = props.shoes.find(x => x.id == id);
    let history = useHistory();

    return(
      <div className="container">
        <박스>
            <제목 className='red'>Detail</제목>
            {/* <제목 색상={'red'}>Detail</제목> */}
        </박스> 
        {inputData}
        {/* <input onChange={(e)=>{ inputData변경(e.target.value)}}/> */}
        {
          alert === true
          ? ( <div className="my-alert2">
                <p>재고가 얼마 남지 않았습니다.</p>
             </div>
          )
          : null
        }

      <div className="row">
        <div className="col-md-6">
          <img src={"https://codingapple1.github.io/shop/shoes"+ (Number(찾은상품.id) +1) +".jpg"} width="100%" />
        </div>

        <div className="col-md-6 mt-4">
          <h4 className="pt-5">{찾은상품.title}</h4>
          <p>{찾은상품.content}</p>
          <p>{찾은상품.price} 원</p>

          <Info 재고={props.재고}></Info>

          <button className="btn btn-danger" onClick={()=>{
            props.재고변경([9,11,12])
          }}>주문하기</button>
          <button className="btn btn-danger" onClick={()=>{
              history.goBack();
          }}>뒤로가기</button>
        </div>
      </div>
    </div>
    )
  }
  
  function Info(props){
    return (
      <p>재고 :{props.재고[0]}</p>
    )
  }

export default Detail;